<!-- Plantilla per a la secció d'alumnes premiats -->
<template>
  <div>
    <!-- Títol amb icones per a alumnes premiats -->
    <h1 class="titol1">
      <icons :icon="['fas', 'award']" /> 
      Alumnes premiats
      <icons :icon="['fas', 'award']" /> 
    </h1>

    <!-- Contingut de la secció amb targetes de projectes premiats -->
    <div class="container-md">
      <b-card-group columns>
        <!-- Iteració pels projectes i ús del component de targeta -->
        <div columns v-for="proyecto in proyectos" :key="proyecto.id">
          <Card2 :nom="proyecto.nom" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :premi="proyecto.premi" :competicio="proyecto.competicio"></Card2> 
        </div>
      </b-card-group>
    </div>
  </div>
</template>

<script>
// Importació del component de targeta
import Card2 from '../components/Card2.vue';

// URL del fitxer JSON amb la informació dels projectes
const lstProysJson = '../proyectosWD2021.json';

export default {
  // Propietats del component
  props: {  
    name: {
      type: String,
      default: 'premiats'
    }
  },
  // Components utilitzats en aquesta pàgina
  components: {
    Card2
  },
  // Dades del component
  data() {
    return {
      proyectos: [], // Array per emmagatzemar la informació dels projectes
      githubPage: 1, // Pàgina per a la càrrega de dades (no utilitzat actualment)
      page: 1, // Pàgina actual
      loading: false, // Indicador de càrrega
      perPage: 5 // Nombre de projectes a mostrar per pàgina
    };
  },
  // Mètodes del component
  mounted() {
    this.fetchData();
  },
  methods: {
    // Mètode per obtenir les dades del fitxer JSON
    async fetchData() {
      try {
        const res = await fetch(lstProysJson);
        const val = await res.json();
        this.proyectos = val;
        console.log(val);
      } catch (error) {
        console.error('Error en obtenir les dades dels projectes:', error);
      }
    }
  }
};
</script>

<style scoped>
/* Estils per al títol */
.titol1 {
  font-size: 250%; 
  font-family: 'Raleway', sans-serif;
  color: #ffffff;
  font-weight: 800;
  margin-bottom: 1%;
  text-align: center;
  padding: 3%;
  position: relative;
  z-index: 2;
}
</style>
